<template>
  <div class="col company-form">
    <div class="row no-gutters">
      <div class="col">
        <div class="title">
          <p>- Отметить объекты, которые смотрели</p>
        </div>
        <div :class="{ 'scroll-list': objects.length > 3 }">
          <button
            class="ml-1 action"
            :class="{ active: negative }"
            :disabled="disabled || negative"
            @click="clickNegative"
          >
            <i class="far fa-frown-open"></i>
            <span class="ml-1">На осмотр не выехал</span>
          </button>
          <button
            class="ml-1 action"
            :disabled="disabled"
            title="Отметить объекты"
            @click="clickSelectObjects"
          >
            <i class="fas fa-plus"></i>
          </button>
          <button
            class="ml-1 action active"
            v-for="object of objects"
            :key="object.id"
            @click="clickOpenObjectInfo"
          >
            <span>{{ object.id }}</span>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Inspection",
  data() {
    return {
      negative: 0,
      objects: [],
    };
  },
  props: {
    actions: {
      type: Object,
      default: () => {
        return {
          negative: 0,
          objects: [],
        };
      },
    },
    branch: {
      type: Number,
    },
    disabled: {
      type: Boolean,
    },
    isConfirmed: {
      type: Boolean,
    },
  },
  mounted() {
    this.setData();
  },
  methods: {
    setData() {
      this.negative = this.actions.negative;
      this.objects = this.actions.objects;
    },
    getData() {
      return {
        negative: this.negative,
        objects: this.objects,
      };
    },
    clickNegative() {
      this.negative = 1;
      this.objects = [];
    },
  },
  watch: {
    disabled() {
      if (!this.isConfirmed) {
        this.setData();
      }
    },
  },
};
</script>

